.loginContainer {
  display: flex;
  width: 100%;
  height: 100vh;
  /* background: url("../../assets/lines.png"); */
}

.removeBg {
  background: none;
}

.scrollContainer {
  display: flex;
  flex: 0.5;
  padding: 5rem;
}
.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.imgContainer {
  display: flex;
  width: 50%;
  height: 30rem;
  /* overflow: hidden; */
  background: url("../../assets/img1.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  animation: slideBg 30s linear infinite 8s;
  animation-timing-function: ease-in-out;
  background-repeat: no-repeat;
}

@keyframes slideBg {
  0% {
    background: url("../../assets/img1.png");
    background-size: contain;
    background-repeat: no-repeat;
  }

  25% {
    background: url("../../assets/img2.png");
    background-size: contain;
    background-repeat: no-repeat;
  }

  50% {
    background: url("../../assets/img3.jpeg");
    background-size: contain;
    background-repeat: no-repeat;
  }

  75% {
    background: url("../../assets/img1.png");
    background-size: contain;
    background-repeat: no-repeat;
  }

  100% {
    background: url("../../assets/img2.png");
    background-size: contain;
    background-repeat: no-repeat;
  }
}
.container h1 {
  color: #9c2780;
  margin-bottom: 0.5rem;
}
.formContainer {
  display: flex;
  flex: 0.5;
  padding: 5rem;
}
.formMainContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.logo {
  display: flex;
  height: 5rem;
  padding: 0.5rem;
}
.logo img {
  max-height: 4rem;
}

.formPadding {
  padding: 1rem 2rem;
}
.formPadding a {
  text-decoration: none;
  color: #5da7ff !important;
  font-size: 12px;
}
.check label {
  font-size: 12px;
}
.check span {
  color: #5da7ff !important;
}

.formPadding p {
  font-size: 12px;
}
.formPadding span {
  font-size: 12px;
}

.bg {
  background: linear-gradient(
    0deg,
    rgba(175, 103, 211, 0.6),
    rgba(156, 39, 128, 0.7)
  );
  color: white;
}
.bg h1 {
  color: white;
}

.formPadding h3 {
  color: #9c2780;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}
.flexContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.forgotContainer {
  width: 25%;
  background: white;
  border-radius: 10px;
  /* position: absolute; */
}
.forgotContainer .formPadding h3 {
  font-size: 1rem;
  color: black;
}
.forgotContainer .formPadding p {
  font-size: 12px;
  color: black;
}
.forgotContainer label,
span {
  color: black;
}
.circle {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: -3rem;
  background: white;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  position: relative;
}
.circle svg {
  font-size: 3rem;
  color: #9c2780;
}
/* Media Query */

@media screen and (min-width: 1300px) and (max-width: 1600px) {
  .imgContainer {
    width: 100%;
    height: 25rem;
  }
}

@media screen and (max-width: 1300px) {
  .imgContainer {
    width: 100%;
    height: 25rem;
  }
}

@media screen and (min-width: 1050px) and (max-width: 1300px) {
  .imgContainer {
    width: 100%;
    height: 20rem;
  }
  .formContainer {
    flex: 0.5;
    width: 100%;
    padding: 1rem 2rem;
  }
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .loginContainer {
    display: flex;
    flex-direction: column;
  }
  .scrollContainer {
    width: 100%;
    padding: 1rem 2rem;

    justify-content: center;
    align-items: center;
  }
  .imgContainer {
    width: 100%;
    height: 15rem;
  }
  .formContainer {
    width: 100%;
    padding: 1rem 2rem;
  }
}

@media screen and (max-width: 700px) {
  .loginContainer {
    display: flex;
    flex-direction: column;
  }
  .scrollContainer {
    width: 100%;
    padding: 1rem 2rem;
    justify-content: center;
    align-items: center;
  }
  .imgContainer {
    width: 100%;
    height: 15rem;
  }
  .formContainer {
    width: 100%;
    padding: 1rem 2rem;
  }
}

@media screen and (max-width: 550px) {
  .loginContainer {
    display: flex;
    flex-direction: column;
  }
  .scrollContainer {
    width: 100%;
    padding: 1rem 2rem;

    justify-content: center;
    align-items: center;
  }
  .imgContainer {
    width: 100%;
    height: 10rem;
  }
  .formContainer {
    width: 100%;
    padding: 1rem 2rem;
  }
}
